import React, { useState, useEffect, useCallback, useRef } from "react";
import { Menu, Moon, Sun } from "lucide-react";
import { Button } from "./ui/button";
import {
  Sheet,
  SheetContent,
  SheetTrigger,
  SheetTitle,
  SheetDescription,
} from "./ui/sheet";
import { navItems, profile } from "../data";
import { cn } from "../lib/utils";
import { gsap, useGSAP } from "../lib/gsap";

type Theme = "light" | "dark";

const Navbar: React.FC = () => {
  const navRef = useRef<HTMLDivElement>(null);
  const [activeSection, setActiveSection] = useState<string>("");
  const [isScrolled, setIsScrolled] = useState(false);
  const [theme, setTheme] = useState<Theme>("light");
  const [isOpen, setIsOpen] = useState(false);
  const [isMounted, setIsMounted] = useState(false);

  // Handle SSR - only show interactive elements after mount
  useEffect(() => {
    setIsMounted(true);
  }, []);

  // Initialize theme on mount
  useEffect(() => {
    if (!isMounted) return;
    const isDark = document.documentElement.classList.contains("dark");
    setTheme(isDark ? "dark" : "light");
  }, [isMounted]);

  // Toggle theme function
  const toggleTheme = useCallback((): void => {
    const newTheme = theme === "dark" ? "light" : "dark";
    setTheme(newTheme);

    if (newTheme === "dark") {
      document.documentElement.classList.add("dark");
      localStorage.setItem("theme", "dark");
    } else {
      document.documentElement.classList.remove("dark");
      localStorage.setItem("theme", "light");
    }
  }, [theme]);

  //  Navbar entrance (delayed to play after hero) 
  useGSAP(
    () => {
      if (!isMounted) return;

      const container = navRef.current;
      if (!container) return;

      // Remove CSS hidden-state classes  GSAP takes over via inline styles.
      const hiddenEls = container.querySelectorAll(".gsap-hidden-fade");

      if (window.matchMedia("(prefers-reduced-motion: reduce)").matches) {
        hiddenEls.forEach((el) => el.classList.remove("gsap-hidden-fade"));
        return;
      }

      hiddenEls.forEach((el) => el.classList.remove("gsap-hidden-fade"));

      // Use fromTo() to supply both start and end values, avoiding
      // forced reflows from GSAP reading getComputedStyle.
      const tl = gsap.timeline({ delay: 1.5 });

      tl.fromTo(
        ".nav-logo",
        { opacity: 0, x: -20 },
        { opacity: 1, x: 0, duration: 0.6, ease: "power3.out" }
      )
        .fromTo(
          ".nav-link",
          { opacity: 0, y: -10 },
          {
            opacity: 1,
            y: 0,
            stagger: 0.08,
            duration: 0.5,
            ease: "power3.out",
          },
          "-=0.3"
        )
        .fromTo(
          ".nav-theme-toggle",
          { opacity: 0, scale: 0.8 },
          {
            opacity: 1,
            scale: 1,
            duration: 0.4,
            ease: "back.out(1.7)",
            clearProps: "all",
          },
          "-=0.2"
        );
    },
    { scope: navRef, dependencies: [isMounted] }
  );

  // Scroll spy using Intersection Observer
  useEffect(() => {
    if (typeof window === "undefined") return;

    const sections = navItems.map((item) => item.href.replace("#", ""));

    const observerOptions = {
      root: null,
      rootMargin: "-20% 0px -60% 0px",
      threshold: 0,
    };

    const observerCallback = (entries: IntersectionObserverEntry[]) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          setActiveSection(entry.target.id);
        }
      });
    };

    const observer = new IntersectionObserver(
      observerCallback,
      observerOptions
    );

    sections.forEach((sectionId) => {
      const element = document.getElementById(sectionId);
      if (element) {
        observer.observe(element);
      }
    });

    return () => observer.disconnect();
  }, []);

  // Handle scroll for navbar background
  useEffect(() => {
    if (typeof window === "undefined") return;

    const handleScroll = () => {
      setIsScrolled(window.scrollY > 20);
    };

    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // Handle navigation click
  const handleNavClick = (
    e: React.MouseEvent<HTMLAnchorElement>,
    href: string
  ) => {
    e.preventDefault();
    const element = document.querySelector(href);
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
      // Update URL without adding to history
      window.history.replaceState(null, "", href);
    }
    setIsOpen(false);
  };

  return (
    <>
      {/* Skip Link */}
      <a
        href="#main-content"
        className="skip-link"
        onClick={(e) => {
          e.preventDefault();
          const main = document.getElementById("main-content");
          if (main) {
            main.focus();
            main.scrollIntoView({ behavior: "smooth" });
          }
        }}
      >
        Skip to main content
      </a>

      {/* Navbar */}
      <header
        ref={navRef}
        className={cn(
          "fixed top-0 left-0 right-0 z-40 transition-all duration-300",
          isScrolled
            ? "bg-bg-light/95 shadow-md backdrop-blur-sm dark:bg-bg-dark/95"
            : "bg-transparent"
        )}
        role="banner"
      >
        <nav
          className="mx-auto flex h-16 max-w-6xl items-center justify-between px-4 sm:px-6 lg:px-8"
          role="navigation"
          aria-label="Main navigation"
        >
          {/* Logo */}
          <a
            href="#hero"
            className="nav-logo gsap-hidden-fade font-display text-2xl font-bold uppercase tracking-[-0.02em] text-text-primary dark:text-text-primary-dark"
            onClick={(e) => handleNavClick(e, "#hero")}
            aria-label={`${profile.handle} - Go to top of page`}
          >
            {profile.handle}
          </a>

          {/* Desktop Navigation */}
          <div className="hidden items-center gap-1 md:flex">
            {navItems.map((item) => (
              <div
                key={item.href}
                className="nav-link gsap-hidden-fade group relative"
              >
                <a
                  href={item.href}
                  onClick={(e) => handleNavClick(e, item.href)}
                  className={cn(
                    "relative px-4 py-2 text-base font-medium transition-colors min-h-[44px] min-w-[44px] inline-flex items-center",
                    activeSection === item.href.replace("#", "")
                      ? "text-accent-orange dark:text-accent-orange-dark"
                      : "text-text-secondary hover:text-text-primary dark:text-text-secondary-dark dark:hover:text-text-primary-dark"
                  )}
                  aria-label={item.ariaLabel}
                  aria-current={
                    activeSection === item.href.replace("#", "")
                      ? "true"
                      : undefined
                  }
                >
                  {item.label}
                  {/* Underline */}
                  <span
                    className={cn(
                      "absolute bottom-0 left-4 right-4 h-0.5 origin-left bg-accent-orange transition-transform duration-200 dark:bg-accent-orange-dark",
                      activeSection === item.href.replace("#", "")
                        ? "scale-x-100"
                        : "scale-x-0 group-hover:scale-x-100"
                    )}
                  />
                </a>
              </div>
            ))}

            {/* Theme Toggle - Desktop */}
            {isMounted && (
              <Button
                variant="ghost"
                size="icon"
                onClick={toggleTheme}
                aria-label={`Switch to ${theme === "dark" ? "light" : "dark"} mode`}
                aria-pressed={theme === "dark"}
                className="nav-theme-toggle gsap-hidden-fade ml-2 hover:scale-105 active:scale-95 transition-transform"
              >
                {theme === "dark" ? (
                  <Moon className="h-5 w-5" aria-hidden="true" />
                ) : (
                  <Sun className="h-5 w-5" aria-hidden="true" />
                )}
              </Button>
            )}
          </div>

          {/* Mobile Navigation */}
          <div className="flex items-center gap-2 md:hidden">
            {/* Theme Toggle - Mobile */}
            {isMounted && (
              <Button
                variant="ghost"
                size="icon"
                onClick={toggleTheme}
                aria-label={`Switch to ${theme === "dark" ? "light" : "dark"} mode`}
                aria-pressed={theme === "dark"}
              >
                {theme === "dark" ? (
                  <Moon className="h-5 w-5" aria-hidden="true" />
                ) : (
                  <Sun className="h-5 w-5" aria-hidden="true" />
                )}
              </Button>
            )}

            {/* Mobile Menu Sheet */}
            <Sheet open={isOpen} onOpenChange={setIsOpen}>
              <SheetTrigger asChild>
                <Button
                  variant="ghost"
                  size="icon"
                  aria-label="Open navigation menu"
                  aria-expanded={isOpen}
                >
                  <Menu className="h-6 w-6" aria-hidden="true" />
                </Button>
              </SheetTrigger>
              <SheetContent side="right" className="w-72">
                <SheetTitle className="sr-only">Navigation Menu</SheetTitle>
                <SheetDescription className="sr-only">
                  Use the links below to navigate to different sections of the
                  page
                </SheetDescription>
                <nav
                  className="mt-8 flex flex-col gap-4"
                  aria-label="Mobile navigation"
                >
                  {navItems.map((item) => (
                    <div key={item.href}>
                      <a
                        href={item.href}
                        onClick={(e) => handleNavClick(e, item.href)}
                        className={cn(
                          "block px-4 py-3 text-lg font-medium transition-colors rounded-lg min-h-[44px]",
                          activeSection === item.href.replace("#", "")
                            ? "bg-bg-hover-light text-accent-orange dark:bg-bg-hover-dark dark:text-accent-orange-dark"
                            : "text-text-secondary hover:bg-bg-hover-light hover:text-text-primary dark:text-text-secondary-dark dark:hover:bg-bg-hover-dark dark:hover:text-text-primary-dark"
                        )}
                        aria-label={item.ariaLabel}
                        aria-current={
                          activeSection === item.href.replace("#", "")
                            ? "true"
                            : undefined
                        }
                      >
                        {item.label}
                      </a>
                    </div>
                  ))}
                </nav>
              </SheetContent>
            </Sheet>
          </div>
        </nav>
      </header>
    </>
  );
};

export default Navbar;
